@import "./theme.scss";

body {
  font-family: Open Sans;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  margin: 0;
  background: $bodyBackground;
  box-sizing: border-box;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #535353;
}

/* Header */
.header {
  color: $primaryColor;
  background: $headerColor;
  height: 60px;
  display: flex;
  align-items: center;
}

.header-title {
  font-size: $headerTitleSize;
  font-family: $headerFont;
  font-weight: 600;
  line-height: 79px;
  color: $headerColor;
  margin: 40px 0;
}

.header-logo {
  width: 100%;
  margin-top: 20px;
  background: url("../images/logo1.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.logo-img {
  width: 150px;
  margin-top: 20px;
}

/* Calculator */

.calculator-wrapper {
  border-radius: 18px 18px;
  margin-bottom: 160px !important;
}

.section-title {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  color: black;
}

.section {
  background-color: white;
}

.section.team {
  border-radius: 18px 18px 0px 0px;
  padding: 50px;
}

.section.optimization{
  padding: 100px 60px 130px;
  background-color: black;
  color: white;
  margin-right: 50px;
  position: relative;

  .section-title {
    font-family: Gotham Pro Bold;
    font-size: 40px;
    line-height: 38px;
    color: white;
  }

  .brex-brand {
    position: absolute;
    top: 40px;
    right: 40px;
    
    span {
      font-family: Open Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
      color: #FFFFFF;
      margin-right: 8px;
    }
  }
}

.section.productivity {
  padding: 50px;
  box-shadow: 0px 27px 45px rgba(98, 98, 98, 0.08);
  position: relative;
}

.section.cost {
  border-radius: 0px 0px 18px 18px;
  padding: 50px;
  box-shadow: 0px 27px 45px rgba(98, 98, 98, 0.08);
}

.section.calculator{
  font-family: Poppins;
  display: flex;
  justify-content: space-between;
  background: #5952FF;
  box-shadow: 0px 27px 45px rgba(98, 98, 98, 0.08);
  border-radius: 20px;
  padding: 35px 50px;
  color: white;

  .wrapper {
    .title {
      font-weight: 600;
      font-size: 18px;
      line-height: 27px;
      opacity: 0.9;
    }

    .value {
      font-weight: bold;
      font-size: 28px;
      line-height: 42px;
    }
  }
}

.container.calculator {
  position: fixed;
  bottom: 10px;
  z-index: 100;
}

.form-group {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  margin-bottom: 0;
  align-items: center;

  &:nth-child(2) {
    margin-top: 25px;
  }

  label {
    margin-bottom: 0;
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #535353;
  }

  .input-group {
    width: 250px;
    justify-content: flex-end;
    border-radius: .25rem;

    &:focus-within {
      .input-group-text {
        border: 1px solid #D90845;
      }
      input {
        border: 1px solid #D90845;
        box-shadow: unset;
        font-family: Poppins;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #565656;
      }

      .input-group-prepend + input {
        border-left: 1px solid #DFE1E6;
      }
    }

    .input-group-text {
      border: 1px solid #DFE1E6;
      background: #FAFBFC;
      color: #97A0AF;
      font-family: Open Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
    }

    input {
      text-align: right;
      background: #FAFBFC;
      border: 1px solid #DFE1E6;
      color: #97A0AF;
    }

    .value {
      font-family: Poppins;
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 38px;
      color: #000000;
    }
  }
}

.slider-wrapper {
  margin-top: 100px;
  padding: 0;

  &:nth-child(2) {
    margin-top: 50px;
  }

  .label, .description{
    font-family: Gotham Pro Bold;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 140%;
  }

  .slider {
    margin-top: 20px;

    .rc-slider-mark {
      top: 50px;

      .rc-slider-mark-text {
        font-family: Gotham Pro;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 13px;

        color: #FFFFFF;

        &:before {
          content: "";
          height: 10px;
          border-left: 2px solid white;
          position: absolute;
          right: 50%;
          top: -20px;
        }

        &:first-child {
          left: 8px !important;

          &:before {
            right: calc(100% - 4px);;
          }
        }

        &:last-child {
          left: calc(100% - 20px) !important;

          &:before {
            right: 0;
          }
        }
      }
    }
  }
}

/* Other*/

.adwords-banner {
  height: 600px;
  display: flex;

  p {
    width: 50%;
    text-align: center;
    margin: auto;
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #000000;
    opacity: 0.3;
  }
}

@media (max-width: 768px) {
  .header {
    margin-bottom: 60px;

    .adwords-banner {
      top: 60px;

      p{
        width: 100%;
      }
    }
  }

  .header-title {
    font-size: 25px;
    line-height: 37px;
  }

  .calculator-wrapper {
    margin-bottom: 20px !important;
  }

  .calculator-row {
    margin: 0;
  }

  .section-title {
    font-size: 22px;
    line-height: 33px;
  }

  .section.team{
    margin: 0;
    padding: 40px 30px;
  }

  .section.productivity{
    margin: 0;
    padding: 40px 30px;
  }

  .section.optimization{
    margin: 0;
    padding: 100px 30px 130px;

    .section-title {
      font-size: 22px;
    }
  }

  .section.cost{
    margin: 0;
    padding: 40px 30px;
  }

  .section.calculator {
    flex-wrap: wrap;
    padding: 40px 30px;
    margin: 0 15px;

    .wrapper {
      width: 50%;

      .title {
        font-size: 16px;
      }

      .value {
        font-size: 23px;
      }
    }
  }

  .slider-wrapper {
    margin-top: 120px;
  }

  .form-group{
    display: block;
    margin-top: 20px;

    .input-group {
      width: 100%;
      justify-content: flex-start;
      margin-top: 8px;
      
      input {
        text-align: left;
      }
    }
  }

  .container.calculator {
    position: relative;
    bottom: 0;
    margin-bottom: 40px;
  }
}